import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld';
import GameStore from '@/components/GameStore';
import Products from '@/components/Products';
import ProductDetail from '@/components/ProductDetail';
import Checkout from '@/components/Checkout';
import CheckoutCartsDetail from '@/components/CheckoutCartsDetail';
import CheckoutCustomerForm from '@/components/CheckoutCustomerForm';
import CheckoutConfirmToPay from '@/components/CheckoutConfirmToPay';
import Coupon from '@/components/Coupon';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      // name: 'GameStore',
      component: GameStore,
      children: [
        {
          path: '',
          name: 'Products',
          component: Products,
        },
        {
          path: '/product-detail/:product',
          name: 'ProductDetail',
          component: ProductDetail,
          props: true,
        },
        {
          path: '/checkout',
          // name: 'Checkout',
          component: Checkout,
          children: [
            {
              path: '',
              name: 'CheckoutCartsDetail',
              component: CheckoutCartsDetail,
            },
            {
              path: 'checkout-customer-form',
              name: 'CheckoutCustomerForm',
              component: CheckoutCustomerForm,
            },
            {
              path: '/checkout-confirm-to-pay/:orderId',
              name: 'CheckoutConfirmToPay',
              component: CheckoutConfirmToPay,
              props: true,
            },
          ],
        },
        {
          path: '/coupon',
          name: 'Coupon',
          component: Coupon,
        },
      ],
    },
  ],
});



