<template>
  <div>
    <div
      class="container d-flex justify-content-center align-items-center mt-3"
    >
      <div class="card bg-light" style="width: 40rem;">
        <div class="card-body d-flex flex-column align-items-center">
          <p class="text-danger h1 text-center mb-4" v-if="time > 0">
            限時優惠
          </p>
          <p class="h5 text-monospace text-center mb-4" v-if="time > 0">
            {{ timer }}
          </p>
          <p class="card-title h1 mb-5 text-success" v-if="time > 0">20% off</p>
          <p class="card-text text-monospace py-2 h3 mb-2" v-if="time > 0">
            Coupon Code:
          </p>
          <span
            class="alert-secondary py-2 px-4 mb-5 text-monospace rounded"
            id="couponCode"
            v-if="time"
            >excited
            <button
              class="copy"
              type="button"
              data-clipboard-target="#couponCode"
              data-clipboard-action="copy"
              data-container="body"
              data-toggle="popover"
              data-placement="bottom"
              data-content="已複製"
            >
              <img src="../assets/images/clippy.svg" alt="" /></button
          ></span>
          <!-- Trigger -->

          <!-- <button
            class="btn"
            data-clipboard-text="Just because you can doesn't mean you should — clipboard.js!"
          >
            Copy to clipboard
          </button> -->

          <p class="h2 text-danger" v-if="time < 0">優惠已截止</p>
        </div>
      </div>
      <!-- <div class="row h-100">
        <div class="col">

        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard';
import $ from 'jquery';

export default {
  data() {
    return {
      nowDate: '',
      dueDate: '',
      timer: '6 Day 6 Hours 6 Minutes 6 Second',
      time: 0,
    };
  },
  created() {
    const vm = this;

    if (vm.time >= 0) {
      setInterval(() => {
        vm.nowDate = Math.floor(Date.now() / 1000);
        vm.dueDate = Math.round(
          new Date('2018-12-31T23:59:59Z').getTime() / 1000,
        );

        vm.time = vm.dueDate - vm.nowDate - 28800; // 結束時間 timestamp - 現在時間 timestamp - 8小時的秒數

        const day = Math.floor(vm.time / (24 * 3600));
        const hour = Math.floor((vm.time % (24 * 3600)) / 3600);
        const minute = Math.floor((vm.time % 3600) / 60);
        const second = Math.floor(vm.time % 60);

        vm.$set(
          vm,
          'timer',
          `${day} Day ${hour} Hours ${minute} Minutes ${second} Seconds`,
        );
      }, 1000);
    }
  },
  mounted() {
    const clipboard = new Clipboard('.copy');

    clipboard.on('success', e => {
      // console.info('Action:', e.action);
      // console.info('Text:', e.text);
      // console.info('Trigger:', e.trigger);
      $('.copy').popover('show');
      setTimeout(() => {
        $('.copy').popover('hide');
      }, 2000);

      e.clearSelection();
    });

    clipboard.on('error', e => {
      console.error('Action:', e.action);
      console.error('Trigger:', e.trigger);
    });
  },
};
</script>



